// 
const pathPre = '/medical'
//  
export default {
    path: pathPre,
    component: resolve => {
        require.ensure([], () => {
            resolve(require('../views/gov/Main.vue'))
        })
    },
    children: [
        //
        {
            path: 'gov',
            name: 'gov-index',
            component: resolve => {
                require.ensure([], () => {
                    resolve(require('../views/gov/Gov.vue'))
                })
            },
            meta: {
                title: ' - ',
                noback: true
            }
        },
        {
            path: 'gov/add',
            name: 'gov-add',
            component: resolve => {
                require.ensure([], () => {
                    resolve(require('../views/gov/GovOperate.vue'))
                })
            },
            meta: {
                title: '',
                noback: false
            }
        },
        {
            path: 'edit/:id',
            name: 'gov-edit',
            component: resolve => {
                require.ensure([], () => {
                    resolve(require('../views/gov/GovOperate.vue'))
                })
            },
            meta: {
                title: '',
                noback: false
            }
        },
        // 
        {
            path: 'user/:gov_id',
            name: 'gov-admin',
            component: resolve => {
                require.ensure([], () => {
                    resolve(require('../views/gov/Admin.vue'))
                })
            },
            meta: {
                title: '',
            }
        },

        //  // 
        // {
        //     path: 'user/:gov_id',
        //     name: 'gov-person',
        //     component: resolve => {
        //         require.ensure([], () => {
        //             resolve(require('../views/gov/Person.vue'))
        //         })
        //     },
        //     meta: {
        //         title: '',
        //     }
        // },
        
        // // 
        {
            path: 'user',
            name: 'user-index',
            component: resolve => {
                require.ensure([], () => {
                    resolve(require('../views/gov/user/Index.vue'))
                })
            },
            meta: {
                title: ' - ',
                noback: true
            }
        },
        // {
        //     path: 'user/add',
        //     name: 'user-add',
        //     component: resolve => {
        //         require.ensure([], () => {
        //             resolve(require('../views/gov/user/Form.vue'))
        //         })
        //     },
        //     meta: {
        //         title: '',
        //         noback: false
        //     }
        // },
        
        //
        {
            path: 'user/edit/:user_id',
            name: 'user-edit',
            component: resolve => {
                require.ensure([], () => {
                    resolve(require('../views/gov/user/Form.vue'))
                })
            },
            meta: {
                title: '',
                noback: false
            }
        },
         //
        {
            path: 'person/edit/:user_id',
            name: 'person-add',
            component: resolve => {
                require.ensure([], () => {
                    resolve(require('../views/gov/user/personForm.vue'))
                })
            },
            meta: {
                title: '',
                noback: false
            }
        },
        //
        {
            path: 'person/edit/:user_id',
            name: 'person-edit',
            component: resolve => {
                require.ensure([], () => {
                    resolve(require('../views/gov/user/personForm.vue'))
                })
            },
            meta: {
                title: '',
                noback: false
            }
        },
    ]
}



